.GameModepage {
  display: flex;
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.AnimationBg {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.placeholder {
  /* Set the background color to transparent */
  background-color: transparent;
}
.GameModett {
  z-index: 1;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 60%;
}
.fourthbut {
  position: absolute;
  align-items: center;
  height: auto;
  width: 20%;
  max-width: 280px;
  top: 45%;
  left: 60%;
  transform: translate(-50%, -50%);
}
.threebut {
  position: absolute;
  align-items: center;
  height: auto;
  width: 20%;
  max-width: 280px;
  top: 45%;
  left: 40%;
  transform: translate(-50%, -50%);
}
.BackBut2 {
  position: absolute;
  align-items: center;
  height: auto;
  width: 20%;
  max-width: 280px;
  top: 91%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.dirt1 {
  z-index: 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 20%;
  left: 50%;
  transform: translateX(-50%);
}
.dirt2 {
  z-index: 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 20%;
  left: 0%;
  transform: translateX(-50%);
}
.dirt3 {
  z-index: 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 20%;
  left: 70%;
  transform: translateX(-50%);
}
.treewstone {
  z-index: 0;
  position: absolute;
  width: auto;
  top: 6%;
  left: 5%;
  height: 75%;
  max-width: 75%;
}
.benchContainer {
  position: sticky;
  width: 750px;
  height: 600px;
  top: 60%;
  left: 65%;
  max-height: 60%;
  max-width: 60%;
}
.benchwbush {
  z-index: 0;
  position: absolute;
  width: auto;
  max-width: 100%;
  width: 100%;
  top: 51%;
  left: 45%;
  transform: translate(-50%, -50%);
  position: absolute;
  height: 37%;
  max-height: 37%;
}
.sleepycat {
  z-index: 0;
  position: absolute;
  width: auto;
  max-width: 100%;
  top: 37%;
  left: 30%;
  position: absolute;
  transform: translate(-50%, -50%);
  max-height: 35%;
}
.ballooncat {
  z-index: 0;
  position: absolute;
  width: auto;
  top: 42%;
  left: 8%;
  height: 35%;
  max-width: 35%;
}
.dogrun {
  z-index: 0;
  position: absolute;
  width: auto;
  z-index: 1;
  top: 56%;
  left: 30%;
  height: 35%;
  max-width: 35%;
  transform: rotateY(180deg);
}
.button-img {
  transition: filter 0.3s ease-in-out, transform 0.3s ease-in-out; /* Smooth transition for the filter property */
}

.button-img:hover {
  transform: scale(1.1);
  filter: brightness(1.2);
}

