.tic-container{
  text-align: center;
}
.title{
  z-index: 1;
  position: fixed;
  max-width: 25%;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title span{
  color: darkgoldenrod;
  padding-left: 15px;
}
.reset{
  z-index: 1;
  position: absolute;
  align-items: center;
  max-width: 280px;
  top: 93%;
  left: 50%;
  transform: translate(-50%, -50%);
}
