}
.sizeS{
  position: absolute;
  max-width: 25%;
  top: 80%;
  left: 45%;
  transform: translate(-50%, -50%);
  transition: margin 0.2s ease-in-out;
}
.sizeM{
  position: absolute;
  max-width: 25%;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sizeL{
  position: absolute;
  max-width: 25%;
  top: 80%;
  left: 55%;
  transform: translate(-50%, -50%);
}
.SIZEBOX{
  z-index: -1;
  position: absolute;
  max-width: 25%;
  top: 77%;
  left: 50%;
  transform: translate(-50%, -50%);
}


.bgboard{
  z-index: -1;
  position: absolute;
  top: 42%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sizeS:hover, .sizeM:hover, .sizeL:hover {
  top: calc(80% - 2px);  
}